header {
  width: 100%;
}
header nav {
  padding: 30px 0px;
}
header nav .logo {
  text-align: left;
}
header nav .bars {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  border: 2px solid #086cf9;
  color: #086cf9;
}
header nav .bars:hover {
  cursor: pointer;
  border-color: #086cf9;
  color: #086cf9;
  transition: 1s;
}
header nav .bars .fa {
  font-size: 16px;
}
header nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 991px) {
  header nav ul {
    width: 100%;
    margin-top: 30px;
    flex-direction: column;
  }
}
header nav ul li {
  margin: 0px 20px;
}
@media (max-width: 991px) {
  header nav ul li {
    width: 100%;
    margin: 10px 0px;
    border-bottom: 1px solid #000;
  }
}
header nav ul li a {
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
  color: #151515;
}
header nav ul li a:hover {
  cursor: pointer;
  transition: 1s;
  color: #1c78ff;
}
header nav button {
  font-size: 16px;
  font-weight: 500;
  line-height: 19px;
  color: white;
  border: none;
  padding: 15px 30px;
  background: #1c78ff;
  border-radius: 10px;
}
header nav button:hover {
  cursor: pointer;
  box-shadow: 2px 2px 8px 2px black;
}
@media (max-width: 991px) {
  header nav button {
    width: 100%;
    border: none;
  }
}
header .header_title {
  width: 100%;
  background: #e7f0fe;
  padding: 100px 0px;
}
header .header_title .left {
  text-align: left;
}
header .header_title .left h1 {
  font-size: 80px;
  font-weight: 600;
  line-height: 90px;
  color: #42484b;
}
@media (max-width: 991px) {
  header .header_title .left h1 {
    font-size: 30px;
    line-height: 40px;
  }
}
header .header_title .left p.text {
  margin-top: 25px;
}
header .header_title .left button {
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;
  color: white;
  border: none;
  padding: 15px 30px;
  background: #1c78ff;
  border-radius: 10px;
  margin-top: 30px;
}
header .header_title .left button:hover {
  cursor: pointer;
}
@media (max-width: 991px) {
  header .header_title .left {
    text-align: center;
  }
}
header .header_title .img {
  text-align: right;
}
header .header_title .img img:hover {
  cursor: pointer;
  transition: 0.3s;
  transform: scale(1.1);
}
@media (max-width: 767px) {
  header .header_title .img img {
    width: 400px;
  }
}
@media (max-width: 991px) {
  header .header_title .img {
    text-align: center;
    margin-top: 30px;
  }
}
@media (max-width: 767px) {
  header .header_title .img {
    text-align: center;
  }
}
header .headerLogin {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: #1c78ff;
  z-index: 11111;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: none;
}
header .headerLogin h3 {
  font-size: 45px;
  font-weight: 700;
  color: black;
  letter-spacing: 2px;
  margin-bottom: 30px;
}
@media (max-width: 991px) {
  header .headerLogin h3 {
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  header .headerLogin h3 {
    font-size: 25px;
    font-weight: 500;
    margin-bottom: 20px;
  }
}
header .headerLogin form {
  text-align: center;
}
header .headerLogin form input {
  width: 400px;
  margin: 30px 0px;
  padding: 15px 20px;
  border-radius: 2px;
  border: 2px solid black;
  background-color: transparent;
  color: white;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
}
header .headerLogin form input:focus {
  outline: none;
}
header .headerLogin form input::placeholder {
  color: white;
  text-transform: capitalize;
  font-size: 20px;
  letter-spacing: 2px;
}
@media (max-width: 767px) {
  header .headerLogin form input {
    width: 250px;
  }
}
header .headerLogin form .pass {
  position: relative;
}
header .headerLogin form .pass .fa {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: black;
  font-size: 18px;
}
header .headerLogin form .pass:hover {
  cursor: pointer;
}
header .headerLogin form button {
  padding: 15px 40px;
  color: white;
  background-color: transparent;
  outline: 2px solid black;
  border: none;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 2px;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}
@media (max-width: 767px) {
  header .headerLogin form button {
    padding: 10px 20px;
  }
}
header .headerLogin form button::after {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0px;
  right: 0px;
  background-color: black;
  transform: translateX(100px);
  z-index: -11;
}
header .headerLogin form button::before {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: black;
  transform: translateX(-100px);
  z-index: -11;
}
header .headerLogin form button:hover::after {
  transition: 1s;
  cursor: pointer;
  transform: translateX(0px);
}
header .headerLogin form button:hover::before {
  transition: 1s;
  cursor: pointer;
  transform: translateX(0px);
}
header .headerLogin .close {
  position: absolute;
  top: 10px;
  right: 30px;
  display: block;
  border: 2px solid black;
  padding: 15px 20px;
  color: white;
  border-radius: 5px;
  font-size: 18px;
  transition: 0.3s;
  cursor: pointer;
}
header .headerLogin .close:hover {
  background-color: black;
  transform: rotate(180deg);
}
